<template>
  <div class="page-box index-box">
    <HeadTop :title="'兑换'"></HeadTop>
    <div class="swap-box">
       <div class="swap">
         <div class="swap-input">
           <div class="title flex-between">
               <div class="text">输入<span class="total-stake" @click="getTotalBlance">全部兑换</span></div>
               <div class="balance">余额: {{balance}}</div>
           </div>
             <van-field
                v-model="amount"
                type="number"
                label=""
                @update:model-value="getOutAmount"
                placeholder="0.0"
            >
                <template #button>
                  <div class="flex-middle right-box">
                    <img src="@/assets/images/ABDX_logo.png" alt="">
                    <span>ABDX</span>
                  </div>
                </template>
            </van-field>
         </div>
         <div class="swap-icon">
            <img src="@/assets/images/swap_icon.png" alt="">
         </div>
         <div class="swap-input">
           <div class="title flex-between">
               <div class="text">输出</div>
           </div>
             <van-field
                v-model="usdtAmount"
                label=""
                readonly
                placeholder="0.0"
            >
                <template #button>
                  <div class="flex-middle right-box">
                    <img src="@/assets/images/USDT_logo.png" alt="">
                    <span>USDT</span>
                  </div>
                </template>
            </van-field>
         </div>
         <div class="price-box">{{rate}} ABDX : 1 USDT</div>
            <van-button type="primary" :loading="btn.loading" v-if="Number(allowance) > 0" size="large" loading-text="正在兑换..." @click="swap">兑换</van-button>
          <van-button type="primary" :loading="btn.loading" v-else size="large" loading-text="正在授权..." @click="approve">授权</van-button>
       </div>
    </div>
    <div class="rule-box">
        <div class="title">兑换规则</div>
        <div class="content">
          1、100ABDX可以兑换1USDT 
        </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop' 
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import { MaxUint256 } from "@ethersproject/constants";
import { Toast } from 'vant';
import { useRouter } from 'vue-router'
import BigNumber from 'bignumber.js';
import _ from 'lodash'
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const router = useRouter()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      investContract: null,
      abdxContract: null,
      usdtContract: null,
      userInfo: {
        intro: '',
        isExist: true,
      },
      btn: {
        loading: false
      },
      amount: '',
      incomeAmount: computed(() => {
        if(data.amount) {
          return Number(data.amount * 3.6).toFixed(2)
        }
        return ''
      }),
      allowance: '',
      balance: '',
      rate: '100',
      staticAmount: '0.00',
      usdtAmount: '',
      initContract: () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.abdxContract = new data.web3.eth.Contract(tokenAbi, config.abdxAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.initData()
      },
      initData: () => {
        data.getUserInfo()
        data.getRate()
        data.getBalance()
        data.getAllowance()
      },
      getUserInfo: async () => {
        let userInfo = await data.investContract.methods.getUserInfo(data.address).call()
        data.userInfo.intro = userInfo.intro
        data.userInfo.isExist = userInfo.isExist
      },
      getRate: async () => {
        let price = await data.investContract.methods.tokenPrice().call();
        data.rate = new BigNumber(price).dividedBy(10000).toFixed()
      },
      getAllowance: async () => {
        let allowance = await data.abdxContract.methods.allowance(data.address, config.investAddress).call()
        data.allowance = allowance;
      },
      getBalance: async () => {
        let decimals = await data.abdxContract.methods.decimals().call()
        let balance = await data.abdxContract.methods.balanceOf(data.address).call()
        data.balance = new BigNumber(balance).dividedBy(10 ** decimals).toFixed(6)
      },
      getTotalBlance: async () => {
        let decimals = await data.abdxContract.methods.decimals().call()
        let balance = await data.abdxContract.methods.balanceOf(data.address).call()
        data.amount = new BigNumber(balance).dividedBy(10 ** decimals).toFixed(Number(decimals))
        data.getOutAmount()
      },
      getOutAmount: _.throttle(async function(){
        if (!data.amount || Number(data.amount)  == 0) {
          return '0.00'
        }
        let decimals = await data.abdxContract.methods.decimals().call()
        let amount = new BigNumber(data.amount).multipliedBy(10 ** decimals).toFixed()
        let outAmount = await data.investContract.methods.getUsdtAmount(amount).call()
        let usdtDecimals = await data.usdtContract.methods.decimals().call()
        let usdtAmount = new BigNumber(outAmount).dividedBy(10 ** usdtDecimals).toFixed(6)
        data.usdtAmount = usdtAmount
      }, 1000),
      approve: async () => {
        data.btn.loading = true
        try {
          await data.abdxContract.methods.approve(config.investAddress, MaxUint256).send({from: data.address})
          Toast('授权成功')
          data.getAllowance()
          data.btn.loading = false
        } catch (error) {
          Toast('授权失败')
          data.btn.loading = false
        }
       
      },
      swap: async () => {
        if(!data.amount) {
          Toast('请先输入数量')
          return
        }
        if(Number(data.amount) > Number(data.balance)) {
          Toast('余额不足')
          return
        }
        if(Number(data.amount) < 1000) {
          Toast('数量需要大于1000')
          return
        }
        data.btn.loading = true
        try {
          let decimals = await data.abdxContract.methods.decimals().call()
          let amount = new BigNumber(data.amount).multipliedBy(10 ** decimals).toFixed()
          await data.investContract.methods.swapByToken(amount).send({from: data.address})
          data.initData()
          Toast('兑换成功')
          data.btn.loading = false
          router.push({name: 'RecordSwap'})
        } catch (error) {
          Toast('兑换失败')
           data.btn.loading = false
        }
       
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
.swap-box{
  padding: 12px 15px;
  .swap{
    background: #fff;
    border-radius: 8px;
    padding: 17px;
    .swap-input{
        background: #F4F4F4;
        border-radius: 8px;
        padding: 15px;
        .title{
          margin-bottom: 15px;
          color: #333333;
          font-size: 14px;
        }
        .right-box{
            img{
              width: 22px;
              margin-right: 5px;
            }
            span{
              color: #111111;
              font-size: 15px;
              font-weight: 600;
            }
        }
    }
    .swap-icon{
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px;
      img{
          width: 35px;
      }
    }
    .price-box{
        text-align: right;
        color: #111111;
        font-size: 13px;
        margin-top: 14px;
        margin-bottom: 16px;
    }
  }
}
.rule-box{
 background: #fff;
 border-radius: 8px;
 padding: 15px;
 .title{
   color: #2058E9;
   font-size: 14px;
   font-weight: 600;
   margin-bottom: 6px;
 }
 .content{
   font-size: 14px;
   line-height: 24px;
   color: #2D1D03; 
 }
}
.total-stake{
  background: #FF9630;
  border-radius: 4px;
  font-size: 10px;
  color: #fff;
  padding: 2px 4px;
  margin-left: 6px;
}
</style>
